export let tag;
let value = tag.value;
let color = tag.color;
let choices = [
    { "value": "ÏàòÎÜìÏùÄÎ≥ÑÎπõ", "color": "#bbb0dc" },
    { "value": "Î≤öÍΩÉÌïëÌÅ¨", "color": "#f1d2e7" },
    { "value": "Í∞ïÏΩîÎûÑ", "color": "#db706c" },
    { "value": "ÏòåÎ°úÏö∞", "color": "#fcf695" },
    { "value": "Ï±àÎ©îÎûÑÎìú", "color": "#a7e0e1" },
    { "value": "Ï±îÎ¨¥", "color": "#cee5d5" },
    { "value": "Î∞±ÏÑ§ÎØºÏ£º", "color": "#fff" },
    { "value": "ÎÇòÎ∂ÄÌÇ§ÌïòÎäò", "color": "#b7d3e9" },
    { "value": "ÌÜ†ÎØ∏Î≤†Ïù¥ÏßÄ", "color": "#f1c3aa" },
    { "value": "Ïú®Î†åÏßÄ", "color": "#f3aa51" },
    { "value": "Î∏îÎ£®ÏßÄÎãà", "color": "#567ace" },
    { "value": "Ï∫îÎîîÌïëÌÅ¨", "color": "#d9598c" },
];
const { close } = getContext("simple-modal");
$: onClick = () => {
    api.AllTagDict.updateTag(tag.value, { value, color });
    if ($selected_tag_value == tag.value) {
        selected_tag_value.set(value);
    }
    all_tag_dict.update(dict => {
        const old_value = tag.value;
        const the_tag = dict.get(tag.value);
        the_tag.value = value;
        the_tag.color = color;
        dict.delete(old_value);
        dict.set(value, the_tag);
        return dict;
    });
    close();
};
const get_dark_text_color = (text_color) => {
    switch (text_color) {
        case "#fff":
            return "#333";
        case "#fcf695":
            return "#333";
        default:
            return "white";
    }
};
</script>

<style>h3 {
  font-size: 1.5rem;
  text-align: center;
}</style>

<div style="text-align: center;">
  <h3 class="{$dark ? 'text-gray-300': 'text-black'}">
    {$_(t.ÌÉúÍ∑∏ÏàòÏ†ï)}
  </h3>
    <input type="text"
    id="TagUpdateInput"
    style="text-align: center;
