    "Ïù¥Ï±ÑÏó∞", "ÍπÄÏ±ÑÏõê", "ÍπÄÎØºÏ£º", "ÏïºÎ∂ÄÌÇ§ ÎÇòÏΩî",
    "ÌòºÎã§ ÌûàÌÜ†ÎØ∏", "Ï°∞Ïú†Î¶¨", "ÏïàÏú†ÏßÑ", "Ïû•ÏõêÏòÅ"
].reduce((acc, member) => {
